<template>
	<view class="container">
		<view class="tops">
			<view :class="showStatus==1?'tab active':'tab'" @click="updataShow(1)">
				<uni-icons type="wallet" size="30" color="#ffaa00"></uni-icons>
				<view class="text">待付款</view>
			</view>
			<view :class="showStatus==2?'tab active':'tab'" @click="updataShow(2)">
				<uni-icons type="shop" size="30" color="#aa007f"></uni-icons>
				<view class="text">待发货</view>
			</view>
			<view :class="showStatus==4?'tab active':'tab'" @click="updataShow(4)">
				<uni-icons type="chatboxes" size="30" color="#00aaff"></uni-icons>
				<view class="text">待评价</view>
			</view>
			<view :class="showStatus==0?'tab active':'tab'" @click="updataShow(0)">
				<uni-icons type="calendar" size="30" color="#55aa7f"></uni-icons>
				<view class="text">全部</view>
			</view>
		</view>
		<view class="list">
			<uni-card class="item" v-for="order in (showStatus==0?orderList:showOrder)" :key="order.id">
				<template v-slot:title>
					<view class="cart-top">
						<text class="left">订单编号：{{order.order_code}}</text>
						<text class="right" v-if="order.status==1" style="color:#e43d33;">待支付</text>
						<text class="right" v-if="order.status==2" style="color:#ffaa00;">待发货</text>
						<text class="right" v-if="order.status==4" style="color:#00aaff;">待评价</text>
					</view>
				</template>
				<view class="cart-goods">
					<view class="goods" v-for="g in order.ordergoods_set" :key="g.goods.id">
						<view class="left">
							<image :src="g.goods.image" mode="" class="img"></image>
							<view class="info">
								<view class="name">{{g.goods.title}}</view>
								<view class="price">￥{{g.price}}</view>
							</view>
						</view>
						<view class="number">x {{g.number}}</view>
					</view>
					<view class="text-box">总计{{order.ordergoods_set.length}}件商品，实付款<text class="text">{{order.amount}}</text>元</view>
				</view>
			    <template v-slot:actions>
			    	<view class="cart-bot">
						<view class="left">
							<text>创建时间：</text>
							<uni-dateformat :date="order.create_time"></uni-dateformat>
						</view>
						<view class="right">
							<button v-if="order.status==1" class="btn" plain size="mini" type="warn" @click="closeOrder(order.id)">取消</button>
							<button v-if="order.status==1" class="btn" size="mini" type="primary" @click="toPay(order.order_code,order.amount)">去支付</button>
							<button v-if="order.status==4" class="btn" size="mini" type="primary">去评价</button>
						</view>
					</view>
			    </template>
			</uni-card>
		</view>

	</view>
</template>

<script>
	export default {
		data() {
			return {
				orderList: [],
				showStatus:0, //1待支付  2待发货  3配送中  4待评价  5已完成  6已关闭
				optionsStatus:0
			}
		},
		onLoad(options) {
			if(options.status){
				this.optionsStatus = options.status
			}
		},
		computed:{
			showOrder(){
				return this.orderList.filter((item)=>{
					return item.status == this.showStatus
				})
			}
		},
		methods: {
			// 获取订单列表
			async getOrderList() {
				let userId = uni.getStorageSync('userInfo').id
				const response = await this.$api.order.getOrderList(userId)
				if (response.data.code === 200) {
					response.data.data.forEach((item)=>{
						item.ordergoods_set = JSON.parse(item.ordergoods_set)
						if(item.pay_time){
							item.pay_time = new Date(item.pay_time).getTime()
						}
					})
					this.orderList = response.data.data
				}
			},
			updataShow(status){
				this.showStatus = status
				if(status==2){
					this.showOrder.sort((a, b) => {
					    return b.pay_time - a.pay_time;
					});
				}
			},
			//取消订单
			async closeOrder(id){
				const response = await this.$api.order.closeOrder(id)
				if (response.data.code === 200) {
					this.getOrderList()
					uni.showToast({
						title:'取消成功'
					})
				}
			},
			toPay(id,total){
				uni.navigateTo({
					url:`/pages/order/pay?id=${id}&&price=${total}`
				})
			}
		},
		onShow(options){
			let that = this
			this.getOrderList().then(res => {
				if(that.optionsStatus){
					that.updataShow(that.optionsStatus)
				}
			})
		}
	}
</script>

<style lang="scss">
	.container {
		padding-bottom: 30upx;
		.tops {
			background-color: #fff;
			display: flex;
			align-items: center;

			.tab {
				display: flex;
				flex-direction: column;
				align-items: center;
				width: 25%;
				padding: 10upx 0;

				.text {
					font-size: 24upx;
					margin-top: 10upx;
				}
			}
			.active{
				position: relative;
				&::after{
					content: '';
					width: 100%;
					height: 4upx;
					background-color: #009000;
					position: absolute;
					bottom: 0;
					left: 0;
				}
			}
		}
		.list{
			.item{
				.cart-top,.cart-bot{
					display: flex;
					align-items: center;
					justify-content: space-between;
					padding: 15upx 0;
				}
				.cart-top{
					font-weight: bold;
					font-size: 30upx;
					border-bottom: 1px #eee solid;
				}
				.cart-goods{
					.goods{
						display: flex;
						align-items: center;
						justify-content: space-between;
						padding: 6upx 0;
						.left{
							display: flex;
							align-items: center;
							.img{
								width: 120upx;
								height: 120upx;
							}
							.info{
								margin-left: 20upx;
								.name{
									color:#333;
									font-size: 30upx;
								}
								.price{
									font-size: 28upx;
									color: #ff557f;
									margin-top: 10upx;
								}
							}
						}
						.number{
							color: #909399;
						}
					}
					.text-box{
						font-size: 28upx;
						font-weight: bold;
						text-align: right;
						padding: 10upx 0 ;
						.text{
							color: #ff557f;
						}
					}
				}
				.cart-bot{
					border-top: 1px #eee solid;
					font-size: 26upx;
					color: #909399;
					.right{
						display: flex;
						align-items: center;
						.btn{
							border-radius: 50upx;
							margin-right: 10upx;
							&:last-child{
								margin: 0;
							}
						}
					}
				}
			}
		}
	}
</style>